/*
 * Helpers SCSS
 * Mixins and Functions
 */
/* Mixin dedicated to headings */
/* Centering element(s) */
/* Media queries */
/*
 * Reset SCSS file
 * CSS resets
 */
thead, tbody, tfoot, table, input, label, button, select, textarea, ol, ul, dd, a, header, nav, section, blockquote, footer, body, html {
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  font-size: 16px;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  color: black;
  line-height: 1.5;
  background-color: white;
  font-size: 1rem;
  font-family: roboto, helvetica, sans-serif;
  font-style: normal;
}

header, nav, section, blockquote, footer {
  font-size: 100%;
}

h6, h5, h4, h3, h2, h1 {
  margin-top: 0;
  margin-bottom: 0.5em;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}

h1 {
  font-family: Inter-semibold, sans-serif;
  font-size: calc(2em + 0.5vw);
}

h2 {
  font-family: Inter-semibold, sans-serif;
  font-size: calc(1.5em + 0.3vw);
}

h3 {
  font-family: Inter-semibold, sans-serif;
  font-size: calc(1.4em + 0.4vw);
}

h4 {
  font-family: Inter-medium, sans-serif;
  font-size: 1.3em;
}

h5 {
  font-family: Inter-medium, sans-serif;
  font-size: 1.25em;
}

h6 {
  font-family: Inter-medium, sans-serif;
  font-size: 1em;
}

p {
  margin: 0;
  padding: 0;
  font-size: 1rem;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

ol, ul, dd {
  list-style: none;
}

img, svg, figure {
  vertical-align: middle;
}

input, label, button, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input, button:focus:not(:focus-visible) {
  outline: none;
  outline-offset: 0;
}

button, select {
  text-transform: none;
}

button, [role=button], [role=submit], [role=reset] {
  appearance: none;
  cursor: pointer;
  background-color: transparent;
}

select {
  word-wrap: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

textarea {
  resize: vertical;
}

iframe {
  border: 0;
}

[hidden] {
  display: none !important;
}

::file-selector-button {
  font: inherit;
}

/** Scrollbars style **/
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #555;
}

/*
 * Layout Index
 */
/*
 * Website Animation definitions
 */
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes slidein {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}
.slidein {
  animation: slidein 0.5s;
}

