/*
 * Dropdowns Component SCSS
 * Dropdowns styles
 */

.dropdown {
    z-index: 1;
    opacity: 0;
    display: block;
    padding: 0;
    border: none;
    border-radius: 0;
    visibility: hidden;
    background: white;
    position: absolute;
    transition: all 0.5s;
    text-align: left;
    box-shadow: 0px 0px 10px rgba(0,0,0,25%);

    * {
        text-align: inherit;
    }
}