/* !
 * Testimonials Component
 * Related styles
 */ 

 #testimonials {
	z-index:1;
	color: white;
	position: relative;
	padding: 50px 0px 70px 0px;
	background-image: linear-gradient(left,rgb(0,0,0,0.6),rgb(0,0,0,0.6)), url("/static/images/gallery/students.jpg.jpg");
	background-image: -webkit-linear-gradient(left,rgb(0,0,0,0.6),rgb(0,0,0,0.6)), url("/static/images/gallery/students.jpg");
	background-position: 0% 70%;
	background-size: cover;

    h2 {
        color: inherit;
        margin-bottom: 30px;
    }

    #testimonials_carousel {
        width: 80%;
        margin: auto;
    }

    .testimony {
        position:relative;
        margin-bottom: 1.7em;

        p {
            font-size: 1.1em;
            text-align: center;
            font-family: 'Inter-thin';

            i {
                padding-bottom: 1em;
                display: block;
            }
        }
    }

    .testimony-author {
        text-align: center;

        img {
            display: inline-block;
            width: 50px;
            height: 50px;
            margin: 0px 10px;
            border-radius: 50%;
        }

        > div {
            display: inline-block;
            vertical-align: middle;

            span {
                display: block;
                text-align: left;
                
                &.testimony-author-name {
                    font-size: 1.2em;
                    line-height: 1.3;
                    text-transform: capitalize;
                }

                &::last-child {
                    margin: 0px;
                    font-size: 1em;
                }
            }
        }
    }
}