/*
 * Header Navigation Bar
 */

%active {
    content: "";
    width: 100%;
    height: 5px;
    background: $color-theme1;
    position: absolute;
    bottom: 0;
    left: 0;
}

header .nav {
    ul {
        float: right;

        li {
            float: left;
            margin: 0;
            margin-left: 10px;
            text-align: center;
            transition: all 0.3s;

            &:hover a::before {
                @extend %active;
            }

            &:not(:last-child) {
                margin-right: 0.5em;
            }

            a {
                position: relative;
                display: block;
                font-family: roboto-medium;
                padding: 25px 20px;
                
                &:hover
                {
                    color: $color-theme1;
                }
            }
        }

        li.active a::before {
            @extend %active;
        }
    }
}